<!-- 资源专辑主页 -->
<template>
    <div class="album">
        <div class="main-content">
            <res-search v-model="searchName" @handleSearch="handleSearch"></res-search>
            <div class="res-boxs">
              <res-box @click.native="handleTurnToDetail(item)" show :info="item" v-for="item in resourcsLists" :key="item.specialId" ></res-box>
            </div>
            <resPagination 
              v-if="pagination.total / pagination.pageSize > 1"
              :currentPage="pagination.currentPage" 
              :total="pagination.total"
              :pageSize="pagination.pageSize"
              @onSizeChange="handlePageChange"
            ></resPagination>
        </div>
        <res-footer></res-footer>
    </div>
</template>

<script>
export default {
  data(){
    return {
      id:0,
      pagination:{
        total:0,
        pageSize:20,
        currentPage:1,
      },
      searchName:'',
      resourcsLists:[]
    }
  },
  components:{
    resBox:()=>import("@/components/res-box"),
    resFooter: () => import("@/components/res-footer"),
    resPagination:()=>import("@/components/res-pagination"),
    resSearch:()=>import("@/components/res-search")
  },
  created(){
    this.id = this.$route.query.id || 0;
    this.getSpecialData()
  },
  methods:{
    getSpecialData(){
      const params = {
        currPage:this.pagination.currentPage,
        pageSize:this.pagination.pageSize,
        specialsName:this.searchName
      }
      this.$api.selectspecialsPage(this.id,params).then(res => {
        this.resourcsLists = res.data;
        this.pagination.total = parseInt(res.totalDatas)
      })
    },
    handlePageChange(currentPage){
      this.pagination.currentPage = currentPage;
      this.getSpecialData();
    },
    handleSearch(){
      this.pagination.currentPage = 1;
      this.getSpecialData();
    },
    handleTurnToDetail(item){
      this.$router.push(`/resource/Layout/album/${item.specialId}`)
    }
  }
}
</script>

<style lang="scss" scoped="scoped">
.album {
    min-width: 1200px;
    background: #F5F6F8;
    overflow: hidden;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .main-content {
        width: 80%;
        margin: 0 auto;
    }
    .res-boxs {
        margin-top: 50px;
        display: flex;
        flex-wrap: wrap;
        // justify-content: space-between;
        & > div {
          width: 20%;
        }
        @media screen and (max-width: 1520px) {
          & > div {
            width: 25%;
          }
        }
    }
}
</style>

